/**
 * Component representing a custom button.
 */
import { Component, Prop, State, h } from '@stencil/core';

@Component({
  tag: 'app-button',
  styleUrl: 'button.component.scss',
  shadow: true,
})
export class ButtonComponent {
  /**
   * The CSS class for the button.
   * @prop {string} classButton - The class name of the button. Defaults to 'primary'.
   */
  @Prop() classButton: string = 'primary';

  /**
   * Indicates whether the button is disabled.
   * @prop {boolean} disabled - Set to `true` to disable the button, `false` otherwise. Defaults to `false`.
   */
  @Prop() disabled: boolean = false;

  /**
   * The type of the button.
   * @prop {string} buttonType - The type attribute of the button. Defaults to 'button'.
   */
  @Prop() buttonType: string = 'button';

  /**
   * The Y position of the mouse cursor.
   * @state {number} mouseY - The Y position of the mouse cursor.
   */
  @State() mouseY: number = 0;

  /**
   * The X position of the mouse cursor.
   * @state {number} mouseX - The X position of the mouse cursor.
   */
  @State() mouseX: number = 0;

  /**
   * Indicates whether to show the button effect.
   * @state {boolean} showEffect - Set to `true` to show the button effect, `false` otherwise.
   */
  @State() showEffect: boolean = false;

  /**
   * Sets the position of the button effect based on the mouse event.
   * @param {MouseEvent} e - The mouse event.
   */
  private setPosition(e: MouseEvent) {
    this.showEffect = false;
    this.mouseY = e.offsetY;
    this.mouseX = e.offsetX;

    setTimeout(() => {
      this.showEffect = false;
    }, 1000);
  }

  render() {
    return (
      <div class={`dav-buttonContainer${this.classButton === 'iconSmall' ? '--row' : ''}`}>
        <button
          class={`dav-button--${this.classButton}`}
          type={this.buttonType}
          disabled={this.disabled}
          name="button"
          onClick={this.setPosition}
        >
          <slot></slot>
          <slot name="icon"></slot>
          {this.showEffect && this.classButton !== 'iconSimple' && this.classButton !== 'back' &&
            <span
              class="dav-button__ripple"
              style={{ '--mouse-y': this.mouseY + 'px', '--mouse-x': this.mouseX + 'px' }}
            ></span>
          }
        </button>
        {(this.classButton === 'iconSmall' || this.classButton === 'iconLarge' || this.classButton === 'iconLargeGrey') &&
          <div class={`dav-button__text${this.classButton === 'iconSmall' ? '--small' : ''}`}>
            <slot name="iconText"></slot>
          </div>
        }
      </div>
    );
  }  
}
